
import React from 'react';
import styles from './Navbar.module.css';

const Navbar = (props) =>{
    return(<div className={styles['main-nav']} >
        <ul className={styles['main-nav__items']}>
        <li className={styles['main-nav__item']}><a className={styles.hyperlink} href={props.url_home}> </a></li>
     
        </ul>
    </div>);
};
export default Navbar;