import styles from './ModalMediaContent.module.css';
import ModalZoomImg from './ModalZoomImg';

const ModalMediaContent = (props) =>{    
    return(
        <div className={styles['content-imgOrvid']}>
            <div className={styles['img-desc']}>  
                {props.vid === true ? 

                <video className={styles['imgOrvid']} controls >
                    <source src={props.media_url} type="video/mp4"></source>
                </video> 
                : 
                
                props.media_url.length === 0 ? 
                        null : <ModalZoomImg source={props.media_url}/>  }                                                                                                       
                {
                props.media_comment_url.length === 0 ? 
                        <p className={styles['img-comment']}>{props.media_comment} </p> : 
                
                props.downloadable === true ? 
                <a className={`${styles['hyperlink']} ${styles['img-comment']}`} href={props.media_comment_url} download> {props.media_comment}</a>
                :
                <a className={`${styles['hyperlink']} ${styles['img-comment']}`} href={props.media_comment_url}> {props.media_comment }</a>
                }
                
                
            </div>
        </div>
    )
}
export default ModalMediaContent;