import styles from './Modal.module.css';
import ModalPanelLeft from './ModalPanelLeft';
import closeicon from '..//../Icons/closeicon.png';
import {Suspense,lazy } from 'react';
import LazyLoad from 'react-lazy-load';

const Modal = (props) =>{        
    if (props.state === false){
        return;        
    };
        
    const close = () =>{
        props.closeModal(false);   
        
    }
      
    
    return(
        <div className={styles['modal-body']}>

        <div onClick={close} className={styles.overlay} ></div>
       
           <div  onClick={close} className={styles['close-button']} ><img className={styles['close-icon']} src={closeicon}/> </div>
          
           <div className={styles['modal-container']}>
                <div className={styles['title-container']}>
                                <h2>{props.modalData.id}</h2>
                </div>
                <div className={styles['close-button']}>
                    <div className={styles['fixed-pos']}>
                    <p className={styles['remove-margin']}></p>
                    </div>
                </div>              
                    {props.modalData.vid === true ?  
                        <video className={styles['image-portrait']} controls >
                            <source src={props.modalData.banner} type="video/mp4"></source>
                        </video>
                        : 
                         <img className={styles['image-portrait']} src={props.modalData.banner} alt=''/>}
                        
            {props.modalData.paragraphs.map((paragraphMap) =>  
            <LazyLoad offset={200}>
                <ModalPanelLeft 
                key = {paragraphMap.id} 
                title = {paragraphMap.title}
                section_title = {paragraphMap.section_title}
                paragraph = {paragraphMap.description}
                media_url = {paragraphMap.media_url}
                vid = {paragraphMap.vid}
                downloadable = {paragraphMap.downloadable}
                media_comment = {paragraphMap.media_comment}
                media_comment_url = {paragraphMap.media_comment_url}
                extra = {paragraphMap.paragraph}
                extra_img = {paragraphMap.paragraph_media}
                />         
           </LazyLoad>         
            )}                        
           
            </div>
        </div>
       
    );

}
export default Modal;