import styles from './Experience.module.css'
import LazyLoad from 'react-lazy-load';
const Experience = (props) =>{
    const setOpenModalHandler = (event) =>{
        props.openModal(true,props.title);
      }
    return(
        <div className={styles['experience-container']}>
            <div className={styles['content-container']}>
                <div className={styles['image-container']}>
                <LazyLoad offset={200}> 
                <button className={styles['button-image']} onClick={setOpenModalHandler} >
                    
                     <img className={styles['fit-image']} alt={props.alt} src={props.url_image}/></button>
                    </LazyLoad>
                </div>
                <div className={styles['title-container']}>
                    <p className={`${styles['center-text']} ${styles['title']}`}> {props.title}</p>
                </div>
                <div className={styles['description-container']} >
                    <p className={`${styles['center-text']} ${styles['description-content']}`}> {props.descriptions}</p>
                    <div className={styles['read-more']}> 
                    <button className={styles['button']} onClick={setOpenModalHandler} > Read more</button>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default Experience;