import ModalMediaContent from './ModalMediaContent';
import styles from './ModalPanelLeft.module.css'
import ModalParagraph from './ModalParagraph';

const ModalPanelLeft = (props) =>{
    
    return(
        <div className={styles['panel-wrapper']}>

            {props.title.length === 0 ? null : 
            <div className={styles['title-container']}>
                {props.section_title === true ? 
                <h3 className={styles['section-title']}>
                    {props.title}
                </h3>
                : 
                <p className={styles['sub-title']}>
                {props.title}
                </p>
                }
                
                </div>
                }                                      
            
                <ModalParagraph text={props.paragraph}/>                                                
                <ModalMediaContent 
                vid = {props.vid}
                media_url={props.media_url} 
                media_comment ={props.media_comment} 
                media_comment_url={props.media_comment_url}
                downloadable = {props.downloadable}
                
                 />
                 <ModalParagraph text={props.extra}/>
                 <ModalMediaContent                
                media_url={props.extra_img} 
                media_comment ='' 
                media_comment_url=''
                downloadable = {false}
                
                 />
              
            
        </div>
   
    )
}
export default ModalPanelLeft;